module.exports = {
  err: function err(msg) {
    throw new Error('Error (Code C' + msg + ')');
  },
  setupColumnize: function setupColumnize(arr, numberColumns, minPerColumn) {
    if (arr === null || !Array.isArray(arr)) this.err(1);
    if (numberColumns == 0) this.err(2);
    if (minPerColumn == 0) this.err(3);
    var firstColumnLimit = numberColumns * minPerColumn;
    var result = {};
    var wia = 1;
    var columnNum = 1;

    for (i = 0; i < arr.length; i++) {
      if (i < firstColumnLimit) {
        if (wia > minPerColumn) {
          columnNum++;
          wia = 1;
        }

        if (result['column' + columnNum] === undefined) result['column' + columnNum] = [];
        result['column' + columnNum].push(arr[i]);
        wia++;
      } else {
        columnNum = i % numberColumns + 1;
        result['column' + columnNum].push(arr[i]);
      }
    }

    return result;
  },
  columnize: function columnize(arr, setup) {
    var columnized = {};
    var columnNum = 1;
    var spot = 0;

    for (x in setup) {
      var column = setup[x];

      for (i = 0; i < column.length; i++) {
        if (columnized['column' + columnNum] === undefined) {
          columnized['column' + columnNum] = [];
        }

        columnized['column' + columnNum].push(arr[spot]);
        spot++;
      }

      columnNum++;
    }

    return columnized;
  },
  columnize2: function columnize2(arr, numberColumns, minPerColumn) {
    var result = {};
    var i = 1;
    var wia = 0;
    var arrLength = arr.length;
    var fence = numberColumns * minPerColumn;

    if (arrLength <= fence) {
      for (var x in arr) {
        if (wia == minPerColumn) {
          wia = 0;
          i++;
        }

        if (result["column" + i] === undefined) result["column" + i] = [];
        result["column" + i].push(arr[x]);
        wia++;
      }
    } else {
      var numRows = Math.ceil(arrLength / numberColumns);
      var numCompleteCols = arrLength % numberColumns;
      var wia = 0;
      if (numCompleteCols == 0) numCompleteCols = numberColumns;

      for (i = 1; i <= numCompleteCols; i++) {
        for (j = 0; j < numRows; j++) {
          if (result["column" + i] === undefined) result["column" + i] = [];
          result["column" + i].push(arr[wia]);
          wia++;
        }
      }

      var nextCol = numCompleteCols + 1;
      var newNumRows = numRows - 1;

      for (i = nextCol; i <= numberColumns; i++) {
        for (j = 0; j < newNumRows; j++) {
          if (result["column" + i] === undefined) result["column" + i] = [];
          result["column" + i].push(arr[wia]);
          wia++;
        }
      }
    }

    return result;
  }
};