var clmnz = require('./src/columnize.js');

var columnize = module.exports = function (arr, numberColumns, minPerColumn) {
  try {
    var result = clmnz.columnize2(arr, numberColumns, minPerColumn);
    var message = '';
  } catch (err) {
    var result = '';
    var message = err.message;
  }

  var data = {
    result: result,
    message: message
  };
  return data;
};